Fancybox.bind("[data-fancybox]", {
    // Your custom options
});

// animation 
const animationItems = document.querySelectorAll('.animation-item');
if (animationItems.length > 0) {
    function onEntry(e) {
        e.forEach(e => {
            e.isIntersecting && e.target.classList.add("animation-active")
        }
        )
    }
    let options = {
        threshold: [.5]
    }, observer = new IntersectionObserver(onEntry, options)
    for (let e of animationItems)
        observer.observe(e);
}
// end animation

let scrollWidthFunc = () => {
    let scrollWidth = window.innerWidth - document.body.clientWidth;
    document.querySelector('html').style.paddingRight = scrollWidth + 'px';
    //document.querySelector('header').style.paddingRight = scrollWidth + 'px';
}

document.addEventListener("DOMContentLoaded", function () {
    
    /* Upload profile photo */
    const profileUploadButton = document.getElementById("profilePhotoUploadBtn");
    const profileFileInput = document.getElementById("profilePhotoUpload");
    const profileImage = document.getElementById("profilePhoto");

    if(profileUploadButton) {
        profileUploadButton.addEventListener("click", function (e) {
            e.preventDefault();
            profileFileInput.click();
        });
    }

    if(profileFileInput) {
        profileFileInput.addEventListener("change", function () {
            const file = profileFileInput.files[0];
            if (file) {
                const reader = new FileReader();
                reader.onload = function (e) {
                    profileImage.src = e.target.result; 
                };
                reader.readAsDataURL(file);
            }
        });
    }
    /* end upload profile photo */


    /* password */
    const newPassword = document.getElementById("newPassword");
    const confirmPassword = document.getElementById("confirmPassword");
    const confirmPasswordLabel = document.getElementById("confirmPasswordLabel");
    const newPasswordLabel = document.getElementById("newPasswordLabel");

    function validateNewPassword() {
        if (newPassword.value.length >= 5) {
            newPasswordLabel.classList.add('true');
        } else {
            newPasswordLabel.classList.remove('true');
        }
    }

    function validatePasswords() {
        if (confirmPassword.value == "") {
            confirmPasswordLabel.textContent = "Пароли не совпадают";
            confirmPasswordLabel.classList.add('false');
        } else {
            if (newPassword.value !== confirmPassword.value) {
                confirmPasswordLabel.textContent = "Пароли не совпадают";
                confirmPasswordLabel.classList.add('false');
            } else {
                confirmPasswordLabel.textContent = "Подтверждение нового пароля";
                confirmPasswordLabel.classList.remove('false');
                confirmPasswordLabel.classList.add('true');
            }
        }
    }

    if(newPassword) {newPassword.addEventListener("input", validateNewPassword); }
    if(newPassword) {newPassword.addEventListener("input", validatePasswords); }
    if(confirmPassword) {confirmPassword.addEventListener("input", validatePasswords); }
    /* end password */


    /*  accordion  */
	const acc = document.getElementsByClassName('accordion')
	for (let i = 0; i < acc.length; i++) {
        if(acc[i]) {
            acc[i].addEventListener('click', function () {
                const accContent = this.querySelector('.accordion__content')  || this.parentElement.querySelector('.accordion__content') 

                if (accContent.classList.contains('active')) {
                    accContent.classList.remove('active');
                    this.classList.remove('active');
                    accContent.style.maxHeight = '0'; 
                } else {
                    accContent.classList.add('active');
                    this.classList.add('active');
    
                    const contentHeight = accContent.scrollHeight;
                    accContent.style.maxHeight = `${contentHeight}px`;
                }
            })
        }
	}
	/*  end accordion   */

    // advantagesCards animation
    const advantagesCards = document.querySelectorAll(".advantages-2__card");
    let current = 0; 
    function highlightCard() {
            advantagesCards.forEach(card => card.classList.remove("active"));
            advantagesCards[current].classList.add("active");
            current = (current + 1) % advantagesCards.length;

    }
    if(advantagesCards.length > 0) {
        setInterval(highlightCard, 3000);
        highlightCard();
    }
    // end advantagesCards animation


    // Фильтрация карточек курсов
    const filterButtons = document.querySelectorAll(".filter-btn");
    const filterCards = document.querySelectorAll(".filter-card");

    // Функция для подсчета карточек курсов каждой категории
    function updateFilterCounts() {
        const counts = { all: filterCards.length };

        filterCards.forEach(card => {
            const category = card.dataset.category;
            counts[category] = (counts[category] || 0) + 1;
        });

        filterButtons.forEach(button => {
            const category = button.dataset.category;
            const countSpan = button.querySelector(".filter-btn span");
            if (countSpan) {
                countSpan.textContent = counts[category] || 0;
            }
        });
    }
    updateFilterCounts();

    filterButtons.forEach(button => {
        button.addEventListener("click", function () {
            filterButtons.forEach(btn => btn.classList.remove("active"));
            this.classList.add("active");
            const selectedCategory = this.dataset.category;
            filterCards.forEach(card => {
                card.style.display = (selectedCategory === "all" || card.dataset.category === selectedCategory) 
                    ? "block" 
                    : "none";
            });
        });
    });
    // end filter


    // start video
    const videosWrap = document.querySelectorAll(".video");
    if(videosWrap) {
        videosWrap.forEach(videoWrap => {
            const video = videoWrap.querySelector("video");
            const playButton = videoWrap.querySelector(".playBtn");
        
            playButton.addEventListener("click", function () {
                if (video.paused) {
                    video.play();
                    playButton.style.display = "none"; 
                    videoWrap.classList.add("hide");
                } else {
                    video.pause();
                    playButton.style.display = "block";
                    videoWrap.classList.remove("hide");
                }
            });
            video.addEventListener("ended", function () {
                playButton.style.display = "block";
                videoWrap.classList.remove("hide");
            });
        });
    }
    // end start video


    // Popups
    function popupClose(popupActive) {
        popupActive.classList.remove('open');
        setTimeout(() => {
            if (!popupActive.classList.contains('open')) {
                popupActive.classList.remove('active');
            }
        }, 400);
        document.body.classList.remove('lock');
        document.querySelector('html').style.paddingRight = 0;
        document.querySelector('html').classList.remove('lock');
    }
    const popupOpenBtns = document.querySelectorAll('.popup-btn');
    const popups = document.querySelectorAll('.popup');
    const closePopupBtns = document.querySelectorAll('.close-popup-btn');
    if(closePopupBtns) {
        closePopupBtns.forEach(function (el) {
            el.addEventListener('click', function (e) {
                popupClose(e.target.closest('.popup'));
            });
        });
    }
    popupOpenBtns.forEach(function (el) {
        el.addEventListener('click', function (e) {
            e.preventDefault();
            const path = e.currentTarget.dataset.path;
            const currentPopup = document.querySelector(`[data-target="${path}"]`);
            if (currentPopup) {
                popups.forEach(function (popup) {
                    popupClose(popup);
                    popup.addEventListener('click', function (e) {
                        if (!e.target.closest('.popup__content')) {
                            popupClose(e.target.closest('.popup'));
                        }
                    });
                });
                currentPopup.classList.add('active');
                setTimeout(() => {
                    currentPopup.classList.add('open');
                }, 10);
                e.stopPropagation();
                scrollWidthFunc();
                document.querySelector('html').classList.add('lock');
            }
        });
    });
    // end popups


    /* pagination for test */
    const contentTest = document.querySelector('.test__list'); 
    const itemsPerPage = 5; // set number of items per page
    let currentPage = 0;

    if (contentTest && contentTest.getElementsByClassName('test__item')) {
        const itemsTest = Array.from(contentTest.getElementsByClassName('test__item')).slice(0);

        function showPage(page) {
            const startIndex = page * itemsPerPage;
            const endIndex = startIndex + itemsPerPage;
            itemsTest.forEach((item, index) => {
                item.classList.toggle('hidden', index < startIndex || index >= endIndex);
            });
            updatePaginationState();
            updateBottomButtons();
        }

        function createPagination() {
            const totalPages = Math.ceil(itemsTest.length / itemsPerPage);
            const paginationContainer = document.querySelector('.pagination');
            paginationContainer.innerHTML = '';
            
            for (let i = 0; i < totalPages; i++) {
                const pageIndicator = document.createElement('span');
                pageIndicator.textContent = i + 1;
                pageIndicator.classList.add('pagination__item');
                paginationContainer.appendChild(pageIndicator);
            }
        }

        function updatePaginationState() {
            const pageIndicators = document.querySelectorAll('.pagination__item');
            pageIndicators.forEach((indicator, index) => {
                indicator.classList.remove('active', 'completed');
                if (index < currentPage) {
                    indicator.classList.add('completed');
                } else if (index === currentPage) {
                    indicator.classList.add('active');
                }
            });
        }

        function updateBottomButtons() {
            const totalPages = Math.ceil(itemsTest.length / itemsPerPage);
            const nextButton = document.querySelector('.test__bottom_next');
            const finishButton = document.querySelector('.test__bottom_finish');
            
            if (currentPage === totalPages - 1) {
                nextButton.style.display = "none";
                finishButton.style.display = "flex";
            } else {
                nextButton.style.display = "flex";
                finishButton.style.display = "none";
            }
        }

        const nextButton = document.querySelector('.test__bottom_next');
        if (nextButton) {
            nextButton.addEventListener('click', () => {
                const totalPages = Math.ceil(itemsTest.length / itemsPerPage);
                if (currentPage < totalPages - 1) {
                    currentPage++;
                    showPage(currentPage);
                    hideSidebarMenu();
                }
            });
        }

        createPagination(); 
        showPage(currentPage);
    }
    /* end pagination for test */



    /*  Скрытие бокового меню с уроками  */
    function hideSidebarMenu() {
        const content = document.querySelector('.content');
        const sidebar = document.querySelector('.sidebar-wrap');
        const sidebarMoreBtn = document.querySelector('.sidebar-more-js');

        if(content && sidebar) {
            const contentHeight = content.scrollHeight;
            const sidebarHeight = sidebar.scrollHeight;
            
            if(sidebarHeight > contentHeight) {
                sidebar.style.maxHeight = `${contentHeight}px`;
                sidebar.classList.add("hide");

                if(sidebarMoreBtn) {
                    sidebarMoreBtn.addEventListener('click', function () {
                        if (sidebar.classList.contains('hide')) {
                            sidebar.classList.remove('hide');
                            sidebarMoreBtn.classList.add('rotate');
                            sidebar.style.maxHeight = `${sidebarHeight + 50}px`;

                            const lessonCourse = document.querySelector('.lesson__course');
                            lessonCourse.style.paddingBottom = '68px';
                        } else {
                            sidebar.classList.add('hide');
                            sidebarMoreBtn.classList.remove('rotate');
                            sidebar.style.maxHeight = `${contentHeight}px`;
                        }
                    })
                }
            }
        }
    }
    hideSidebarMenu();
	/*  end скрытие бокового меню   */


    /* clear input search */
    const formSearch = document.querySelector(".form-search");
    if(formSearch) {
        const input = formSearch.querySelector(".form-search__input");
        const clearBtn = formSearch.querySelector(".form-search__btn");
        function toggleClearButton() {
            clearBtn.style.display = input.value ? "block" : "none";
        }
        input.addEventListener("input", toggleClearButton);
        clearBtn.addEventListener("click", function () {
            input.value = "";
            toggleClearButton();
            input.focus();
            document.querySelectorAll('.popup-students__item').forEach(item => {
                item.classList.remove('none');
            })
        });
        toggleClearButton();
    }
    /* end clear input search */


    /*  search */
	const inputSearch = document.querySelectorAll('input[type=search]')
	if (inputSearch.length > 0) {
        inputSearch.forEach(elem => {
            const wrapper = elem.closest('.search-wrapper')
			if (wrapper) {
				function search() {
                    let filter = elem.value.toUpperCase()
					let ul = wrapper.querySelectorAll('.search-list')
                    
					ul.forEach(item => {
                        let li = item.getElementsByTagName('li')
						for (let i = 0; i < li.length; i++) {
                            let a = li[i].querySelector('.search-name')
                            console.log(a)
							if (a.innerHTML.toUpperCase().indexOf(filter) > -1) {
								li[i].classList.remove('none')
							} else {
								li[i].classList.add('none')
							}
						}
					})
				}
				elem.addEventListener('input', search)
			}
		})
	}
    /*  end search  */  


    /* Изменение списка учеников в попапе */
    const popupStudents = document.querySelectorAll(".popup-students__item");
    if(popupStudents) {
        popupStudents.forEach(student => {
            const btnAddStudent = student.querySelector(".popup-students__item_btn-add");
            const btnDelStudent = student.querySelector(".popup-students__item_condition");
            if(btnAddStudent) {
                btnAddStudent.addEventListener("click", function () {
                    student.classList.add('active');
                });
            }
            if(btnDelStudent) {
                btnDelStudent.addEventListener("click", function () {
                    student.classList.remove('active');
                });
            }
        })
    }
    /* end Изменение списка учеников в попапе */

    /* filter element by select */
    const filterBlocks = document.querySelectorAll('.filter-block');

    filterBlocks.forEach(block => {
        const selects = block.querySelectorAll('.filter-select');
        const cards = block.querySelectorAll('.filter-row');

        function filterCards() {

            cards.forEach(card => {
                let isVisible = true;
                selects.forEach(select => {
                    const filterType = select.getAttribute('data-filter-type');
                    const selectedValue = select.value;
                    const cardValue = card.getAttribute(`data-${filterType}`);
                    if (selectedValue !== 'all' && selectedValue !== cardValue) {
                        isVisible = false;
                    }
                });
                card.style.display = isVisible ? '' : 'none';
            });
        }
        selects.forEach(select => {
            select.addEventListener('change', filterCards);
        });
        filterCards();
    });
    /* end filter element by select */


    /*  tabs */
    const tabs = document.querySelectorAll('.tabs');
    for (let i = 0; i < tabs.length; i++) {
        const tabBtns = tabs[i].querySelectorAll('.tab-btn');
        const tabContents = tabs[i].querySelectorAll('.tab-content');
        for (let i = 0; i < tabBtns.length; i++) {
            tabBtns[i].style.order = 2 * i + 1;
        }
        for (let i = 0; i < tabContents.length; i++) {
            tabBtns[i].style.order = 2 * i + 2;
        }
        if (tabBtns.length > 0) {
            for (let i = 0; i < tabBtns.length; i++) {
                tabBtns[i].addEventListener('click', ()=> {
                    if (!tabBtns[i].classList.contains('active')) {
                        tabContents.forEach(elem => {
                            elem.classList.remove('active');
                        })
                        tabBtns.forEach(elem => {
                            elem.classList.remove('active');
                        })
                        tabBtns[i].classList.add('active');
                        tabContents[i].classList.add('active');
                    }
                })
            }
        }
        if (tabBtns.length > 0) {
            tabBtns[0].click();
        }
    }
    /* end tabs */


    /* Swipers */
    const introSwiper = new Swiper(".introSwiper", {
        direction: "vertical",
        slidesPerView: 3,
        spaceBetween: 16,
        pagination: {
            el: ".swiper-pagination",
            clickable: true,
        },
        navigation: {
            nextEl: ".intro__swiper-button-next",
            prevEl: ".intro__swiper-button-prev",
        },
    });

    const advantagesSwiper = new Swiper(".advantagesSwiper", {
        slidesPerView: 1.2,
        spaceBetween: 10,
        loop: true,
        centeredSlides: true,
        autoplay: {
            delay: 3000,
            disableOnInteraction: false,
        },
        breakpoints: {
            1050: {
                slidesPerView: 3,
                spaceBetween: 40,
            },
            600: {
                slidesPerView: 2,
                spaceBetween: 20,
            },
        },
    });

    const advantagesImgSwiper = new Swiper(".advantagesImgSwiper", {
        slidesPerView: 1,
        spaceBetween: 6,
        loop: false,
        centeredSlides: true,

        allowTouchMove: false, 
    });

    // Связываем слайдеры друг с другом
    advantagesSwiper.on('slideChange', function () {
        let realIndex = advantagesSwiper.realIndex; // Получаем корректный индекс
        advantagesImgSwiper.slideTo(realIndex); // Переключаем второй слайдер
    });


    /* Charts */


    // фон графика
    const backgroundColorPlugin = {
        id: 'customCanvasBackgroundColor',
        beforeDraw: (chart, args, options) => {
          const { ctx, chartArea } = chart;
          const borderRadius = 10; 
          const borderColor = options.borderColor || "#DBE9F7"; 
          const borderWidth = 1; 
          const bgColor =  options.color || "#F0F5FA"; 
  
          ctx.save();
          ctx.fillStyle = bgColor;
          ctx.strokeStyle = borderColor;
          ctx.lineWidth = borderWidth;
  
          // Координаты
          const x = chartArea.left;
          const y = chartArea.top - 10;
          const width = chartArea.right - chartArea.left +10;
          const height = chartArea.bottom - chartArea.top + 10;
  
          // Рисуем закругленный прямоугольник
          ctx.beginPath();
          ctx.moveTo(x + borderRadius, y);
          ctx.lineTo(x + width - borderRadius, y);
          ctx.quadraticCurveTo(x + width, y, x + width, y + borderRadius);
          ctx.lineTo(x + width, y + height - borderRadius);
          ctx.quadraticCurveTo(x + width, y + height, x + width - borderRadius, y + height);
          ctx.lineTo(x + borderRadius, y + height);
          ctx.quadraticCurveTo(x, y + height, x, y + height - borderRadius);
          ctx.lineTo(x, y + borderRadius);
          ctx.quadraticCurveTo(x, y, x + borderRadius, y);
          ctx.closePath();
  
          ctx.fill(); // Заливаем фон
          ctx.stroke(); // Рисуем границу
  
          ctx.restore();
        }
    };

    if (document.querySelector('#timeChart')) {
        // График Время прохождения уроков
        var xValues = [
            'Урок 1',
            'Урок 2',
            'Урок 3',
            'Урок 4',
            'Урок 5',
            'Урок 6',
            'Урок 7',
            'Урок 8',
            'Урок 9',
            'Урок 10',
            'Урок 11',
            'Урок 12'
        ];
        var yValues = [20,15,45,37,60,48,53,26,37,50,33,17];
        new Chart("timeChart", {
            type: "line",
            data: {
                label: "",
                labels: xValues,
                datasets: [{
                    // line
                    borderWidth: 1,
                    borderColor: "#0062C7",
                    tension: 0,
                    // point
                    pointRadius: 6,
                    pointBorderWidth: 3,
                    pointBackgroundColor: "#0062C7",
                    pointBorderColor: "#FFFFFF",
                    hoverRadius: 10,
                    hoverBorderWidth: 5,
                    hoverBorderColor: "#FFFFFF",
                    
                    fill: false,
                    data: yValues
                }]
            },
            options: {
                scales: {
                    x: {
                        offset: 10,
                        grid: {
                            drawOnChartArea: false, 
                            tickColor: "#fff",
                            tickLength: 16,
                        },
                        border: {
                            display: false,
                        }
                    },
                    y: {
                        offset: 10,
                        min: 0,
                        grid: {
                            color: "#DBE9F7",
                            tickColor: "#fff",
                            tickLength: 16,
                        },
                        border: {
                            display: false,
                        },
                        ticks: {
                            callback: function(value) {
                                return value + " мин"; 
                            }
                        }
                    }
                },
                plugins: {
                    legend: {
                        display: false,
                    },
                    customCanvasBackgroundColor: {
                        color: '#f0f5fa',
                        borderColor: '#DBE9F7',
                    },
                    tooltip: {
                        enabled: true, 
                        backgroundColor: '#FFFFFF', 
                        titleColor: '#1A1A1A', 
                        bodyColor: '#1A1A1A', 
                        caretSize: 10,
                        padding: 8,
                        displayColors: false,
                        callbacks: {
                            label: function(tooltipItem) {
                                return  tooltipItem.raw + ' мин';
                            }
                        }
                    }
                },
            },
            plugins: [backgroundColorPlugin],
        });
    }
    

    if (document.querySelector('#testChart')) {
        // График Результаты прохождения тестов
        var xTestValues = ['Тест 1','Тест 2','Тест 3','Тест 4','Тест 5','Тест 6'];
        var yTestValues = [8,21,15,18,10,20];
        new Chart("testChart", {
            type: "line",
            data: {
                labels: xTestValues,
                datasets: [{
                    // line
                    borderWidth: 1,
                    borderColor: "#20CA89",
                    tension: 0,
                    // point
                    pointRadius: 6,
                    pointBorderWidth: 3,
                    pointBackgroundColor: "#20CA89",
                    pointBorderColor: "#FFFFFF",
                    hoverRadius: 10,
                    hoverBorderWidth: 5,
                    hoverBorderColor: "#FFFFFF",
                    
                    fill: false,
                    data: yTestValues
                }]
            },
            options: {
                scales: {
                    x: {
                        offset: 10,
                        grid: {
                            drawOnChartArea: false, 
                            tickColor: "#fff",
                            tickLength: 16,
                        },
                        border: {
                            display: false,
                        }
                    },
                    y: {
                        offset: 10,
                        min: 0,
                        grid: {
                            color: "rgba(32, 202, 137, 0.15)",
                            tickColor: "#fff",
                            tickLength: 16,
                        },
                        border: {
                            display: false,
                        },
                        ticks: {
                            callback: function(value) {
                                return value + " б"; 
                            }
                        }
                    },
                },
                plugins: {
                    legend: {
                        display: false,
                    },
                    customCanvasBackgroundColor: {
                        color: 'rgba(32, 202, 137, 0.1)',
                        borderColor: 'rgba(32, 202, 137, 0.15)',
                    },
                    tooltip: {
                        enabled: true, 
                        backgroundColor: '#FFFFFF', 
                        titleColor: '#1A1A1A', 
                        bodyColor: '#1A1A1A', 
                        caretSize: 10,
                        padding: 8,
                        displayColors: false,
                        callbacks: {
                            label: function(tooltipItem) {
                                return  tooltipItem.raw + ' б';
                            }
                        }
                    }
                },
            },
            plugins: [backgroundColorPlugin],
        });
    }
    /* end charts */

})


